import React from "react";
import { useTranslation } from "react-i18next";

const Header01 = () => {
  const { i18n } = useTranslation();
  const { t } = useTranslation();
  const chg = (ln) => {
    localStorage.setItem("language", ln);
    i18n.changeLanguage(ln);
    const myDiv = document.querySelector('.sidebarBtn');
    myDiv?.click();
  };
  return (
    <>
      <header className="header d-none d-lg-block sticky-on">
        <div id="sticky-placeholder"></div>
        <div
          id="navbar-wrap"
          className="header-bottom container-custom navbar-wrap white-bg"
        >
          <div className="container-fluid">
            <div className="header-bottom__row">
              <div className="header-bottom__col">
                <div className="logo">
                  <a href="/" className="logo__link">
                    <img
                      src="assets/img/logo-webforce-black.png"
                      style={{ maxWidth: "300px" }}
                      alt="IT Solution Techkit"
                    />
                  </a>
                </div>
              </div>
              <div className="header-bottom__col">
                <div className="main-menu">
                  <nav className="main-menu__nav">
                    <ul>
                      <li className="main-menu__nav list">
                        <a className="animation" href="/">
                          {t("Header.home")}
                        </a>
                      </li>
                      <li className="list">
                        <a className="animation" href="/about">
                          {t("Header.about")}
                        </a>
                      </li>
                      <li className="list">
                        <a className="animation" href="/team">
                          {t("Header.team")}
                        </a>
                      </li>
                      <li className="list">
                        <a className="animation" href="/services">
                          {t("Header.service")}
                        </a>
                      </li>
                      <li className="list">
                        <a className="animation" href="/cases">
                          {t("Header.case")}
                        </a>
                      </li>
                      <li className="list">
                        <a className="animation" href="/contact">
                          {t("Header.contact")}
                        </a>
                      </li>
                      <li className="main-menu__nav_sub list">
                        <a className="animation">{t("Header.lng")}</a>
                        <ul className="main-menu__dropdown">
                          <li><a onClick={() => chg("en")}>English</a></li>
                          <li><a onClick={() => chg("zh")}></a></li>
                        </ul>
                      </li>
                    </ul>
                  </nav>
                </div>
              </div>
              <div className="header-top__social">
                <ul>
                  <li className="header-top__social_list">
                    <a
                      className="header-top__social_list--link twitter"
                      href="https://twitter.com/WebforceLab"
                      target={"_blank"}
                    >
                      <i className="fab fa-twitter"></i>
                    </a>
                  </li>
                  <li className="header-top__social_list">
                    <a
                      className="header-top__social_list--link instagram"
                      href="javascript:void(0);"
                    >
                      <i className="fab fa-instagram"></i>
                    </a>
                  </li>
                  <li className="header-top__social_list">
                    <a
                      className="header-top__social_list--link telegram"
                      href="https://t.me/linmetaverse"
                    >
                      <i className="fab fa-telegram"></i>
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </header>
      <div
        className="rt-header-menu mean-container position-relative"
        id="meanmenu"
      >
        <div className="mean-bar">
          <a href="/">
            <img
              className="logo-small"
              alt="Techkit"
              src="assets/img/logo-webforce-black.png"
              style={{ maxWidth: "200px" }}
            />
          </a>
          <form className="header-bottom__col">
            <div className="header-search">
              <input
                placeholder="Search"
                type="text"
                className="header-search__input"
              />
              <button className="header-search__button">
                <i className="fas fa-search"></i>
              </button>
            </div>
          </form>
          <span className="sidebarBtn">
            <span className="fa fa-bars"></span>
          </span>
        </div>
        <div className="rt-slide-nav">
          <div className="offscreen-navigation">
            <nav className="menu-main-primary-container">
              <ul className="menu">
                <li className="list menu-item-parent">
                  <a className="animation" href="/">
                    {t("Header.home")}
                  </a>
                </li>
                <li className="list menu-item-parent menu-item-has-children">
                  <a className="animation" href="/about">
                    {t("Header.about")}
                  </a>
                </li>
                <li className="list">
                  <a className="animation" href="/team">
                    {t("Header.team")}
                  </a>
                </li>
                <li className="list">
                  <a className="animation" href="/services">
                    {t("Header.service")}
                  </a>
                </li>
                <li className="list">
                  <a className="animation" href="/cases">
                    {t("Header.case")}
                  </a>
                </li>
                <li className="list menu-item-parent">
                  <a className="animation" href="/contact">
                    {t("Header.contact")}
                  </a>
                </li>
                <li className="list menu-item-parent menu-item-has-children">
                  <a className="animation">{t("Header.lng")}</a>
                  <ul className="main-menu__dropdown sub-menu">
                    <li><a onClick={() => chg("en")}>English</a></li>
                    <li><a onClick={() => chg("zh")}></a></li>
                  </ul>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </>
  );
};

export default Header01;
