import React from "react";
import { useTranslation } from "react-i18next";

const Banner01 = () => {
  const { t } = useTranslation();
  return (
    <>
      <section
        className="hero hero--padding__software hero--bg__software overflow-hidden"
        data-bg-image="assets/images/shape/home-software-bg.png"
      >
        <div className="container-fluid">
          <div className="row align-items-center">
            <div className="col-lg-5 col-md-8 mb-30">
              <div className="hero-content">
                <span
                  className="hero-content__title_small wow fadeInUp animated"
                  data-wow-delay=".4s"
                >
                  {t("Home.slogan1")}
                </span>
                <h1
                  className="hero-content__title_big wow fadeInUp animated"
                  data-wow-delay=".6s"
                >
                  {t("Home.slogan2")}{' '}
                  <span className="primary-text-color">{t("Home.slogan2_1")}</span>
                  {t("Home.slogan2_2")}
                </h1>
                <p
                  className="hero-content__paragraph wow fadeInUp animated"
                  data-wow-delay=".8s"
                >
                  {t("Home.slogan3")}
                </p>
                <a
                  href="about-us.html"
                  className="btn btn--common btn--primary rt-button-animation-out wow fadeInUp animated"
                  data-wow-delay="1s"
                >
                  {t("Home.slogan4")}
                  <svg
                    width="34px"
                    height="16px"
                    viewBox="0 0 34.53 16"
                    xmlSpace="preserve"
                  >
                    <rect
                      className="rt-button-line"
                      y="7.6"
                      width="34"
                      height=".4"
                    ></rect>
                    <g className="rt-button-cap-fake">
                      <path
                        className="rt-button-cap"
                        d="M25.83.7l.7-.7,8,8-.7.71Zm0,14.6,8-8,.71.71-8,8Z"
                      ></path>
                    </g>
                  </svg>
                </a>
              </div>
            </div>
            <div className="col-lg-7 mb-30">
              <div className="hero-img hero-img--right hero-img--animation">
                <img
                  className="image wow zoomIn animated"
                  data-wow-delay=".1s"
                  src="assets/images/vector/Vector-Shape-Hero-2.png"
                  alt="Vector Image"
                  width="929"
                  height="608"
                />
                <ul className="hero-img__animation_list js-tilt">
                  <li
                    className="item item-1 wow fadeInLeft animated"
                    data-wow-delay=".3s"
                  >
                    <img src="assets/images/vector/Man.png" alt="Animation" />
                  </li>
                  <li
                    className="item item-2 wow fadeInUp animated"
                    data-wow-delay=".5s"
                  >
                    <img src="assets/images/vector/Women.png" alt="Animation" />
                  </li>
                  <li
                    className="item item-3 wow zoomIn animated"
                    data-wow-delay=".7s"
                  >
                    <img
                      src="assets/images/vector/Terget.png"
                      alt="Animation"
                    />
                  </li>
                  <li
                    className="item item-4 wow fadeInRight animated"
                    data-wow-delay=".9s"
                  >
                    <img
                      src="assets/images/vector/setting.png"
                      alt="Animation"
                    />
                  </li>
                  <li
                    className="item item-5 wow fadeInDown animated"
                    data-wow-delay="1.1s"
                  >
                    <img src="assets/images/vector/book.png" alt="Animation" />
                  </li>
                  <li
                    className="item item-6 wow zoomIn animated"
                    data-wow-delay="1.3s"
                  >
                    <img src="assets/images/vector/Blub.png" alt="Animation" />
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Banner01;
