import React from "react";
import { useTranslation } from "react-i18next";

const About01 = () => {
  const { t } = useTranslation();
  return (
    <>
      <section className="about about-agency about--inner">
        <div className="container">
          <div className="row">
            <div className="col-lg-5 mb-30">
              <div className="about-image about-image--agency position-relative">
                <img
                  src="assets/images/home3/about-thumb-agency1.jpg"
                  alt="About"
                  className="about-image__agency_big wow fadeInDown mb-30"
                  data-wow-delay=".2s"
                />
                <img
                  src="assets/images/home3/about-thumb-agency2.jpg"
                  alt="About"
                  className="about-image__agency_small wow fadeInUp mb-30"
                  data-wow-delay=".4s"
                />
                <div className="video">
                  {/* <a
                    className="video-link"
                    href="https://youtu.be/yJg-Y5byMMw"
                    data-fancybox
                  >
                    <i className="fas fa-play"></i>
                  </a> */}
                </div>
              </div>
            </div>
            <div className="col-lg-6 offset-lg-1 mb-30">
              <div className="about-agency__right">
                <div className="section-software p-0 mb-20">
                  <span className="section-software__title_small text-uppercase primary-text-color">
                  {t("About.ex_title")}
                  </span>
                  <h2 className="section-software__title_big">
                  {t("About.ex_subtitle")}
                  </h2>
                </div>
                <div className="about-software__content about-agency__content">
                  <span className="title title--small">
                  {t("About.ex_content")}
                  </span>
                </div>
                <div className="list">
                  <ul>
                    <li>
                      <i className="fas fa-check"></i>{t("About.ex_sec1_title")}
                    </li>
                    <li className="paragraph">
                    {t("About.ex_sec1_content")}
                    </li>
                    <li>
                      <i className="fas fa-check"></i>{t("About.ex_sec2_title")}
                    </li>
                    <li className="paragraph">
                    {t("About.ex_sec2_content")}
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default About01;
