import React from "react";
import { useTranslation } from "react-i18next";

const Footer01 = () => {
  const { t } = useTranslation();

  return (
    <>
      <footer className="footer position-relative footer--top-software--bg">
        <img
          className="footer-shape footer-shape--bottom wow fadeInUp"
          data-wow-delay="1s"
          src="assets/images/shape/footer-software-shape.png"
          alt="Shape"
        />
        <div className="footer--top pt-85 pb-100">
          <div className="container">
            <div className="row">
              <div className="col-sm-12">
                <div className="footer--top__software_top footer--top__software_top--border pb-15 pt-55">
                  <div className="row">
                    <div className="col-lg-6 col-md-6 col-sm-6 mb-30">
                      <div className="footer-widget__log footer--logo__software">
                        <a href="software-innovation.html">
                          <img
                            src="assets/img/logo-webforce-white.png"
                            style={{ maxWidth: "300px" }}
                            alt=""
                          />
                        </a>
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-6 mb-30">
                      <div className="footer-widget__social footer-widget__software_social">
                        <ul>
                          <li className="footer-widget__social_list">
                            <a
                              className="footer-widget__social_list--link twitter"
                              href="https://twitter.com/WebforceLab"
                              target={"_blank"}
                            >
                              <i className="fab fa-twitter"></i>
                            </a>
                          </li>
                          <li className="footer-widget__social_list">
                            <a
                              className="footer-widget__social_list--link instagram"
                              href="javascript:void(0);"
                            >
                              <i className="fab fa-instagram"></i>
                            </a>
                          </li>
                          <li className="footer-widget__social_list">
                            <a
                              className="footer-widget__social_list--link telegram"
                              href="https://t.me/linmetaverse"
                            >
                              <i className="fab fa-telegram"></i>
                            </a>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12">
                <div className="footer__software_middle pt-95">
                  <div className="row">
                    <div className="col-lg-2 col-md-6 col-sm-6 mb-25">
                      <div className="footer-widget footer-widget--software">
                        <div className="footer-widget__title">
                          <h3 className="footer-widget__title_heading heading">
                            {t("Home.services_title")}
                          </h3>
                        </div>
                        <div className="footer-widget__menu footer-widget--software__menu">
                          <ul>
                            <li className="footer-widget__menu_list">
                              <a href="/services">{t("Home.services_1")}</a>
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-2 col-md-6 offset-lg-1 col-sm-6 mb-25">
                      <div className="footer-widget footer-widget--software">
                        <div className="footer-widget__title">
                          <h3 className="footer-widget__title_heading heading">
                            {t("Home.link_title")}
                          </h3>
                        </div>
                        <div className="footer-widget__menu footer-widget--software__menu">
                          <ul>
                            <li className="footer-widget__menu_list">
                              <a href="/about">{t("Home.link_1")}</a>
                            </li>
                            <li className="footer-widget__menu_list">
                              <a href="/team">{t("Home.link_2")}</a>
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-2 col-md-6 offset-lg-1 col-sm-6 mb-25">
                      <div className="footer-widget footer-widget--software">
                        <div className="footer-widget__title">
                          <h3 className="footer-widget__title_heading heading">
                            {t("Home.support_title")}
                          </h3>
                        </div>
                        <div className="footer-widget__menu footer-widget--software__menu">
                          <ul>

                            <li className="footer-widget__menu_list">
                              <a href="/contact">{t("Home.support_content")}</a>
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-3 col-md-6 offset-lg-1 col-sm-6 mb-25">
                      <div className="footer-widget footer-widget--software">
                        <div className="footer-widget__title">
                          <h3 className="footer-widget__title_heading heading">
                            {t("Home.touch_title")}
                          </h3>
                        </div>
                        <div className="footer__software_address">
                          <ul>
                            <li>
                              <strong>{t("Home.touch_2")}:</strong>
                              <span>webforcelab9@gmail.com</span>
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="footer--bottom footer--bottom-padding footer--bottom-software--bg">
          <div className="container">
            <div className="row">
              <div className="col-sm-12">
                <div className="footer__copyright">
                  <p className="footer__copyright_text">
                     2023 <a href="software-innovation.html">Webforce</a>. All
                    Rights Reserved
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </footer>
    </>
  );
};

export default Footer01;
